    }
}

@mixin min-medium() {
    @media (min-width:$width-medium) {
        @content;
    }
}

@mixin min-marge() {
    @media (min-width:$width-marge) {
        @content;
    }
}

@mixin min-_960() {
    @media (min-width:$width-960) {
        @content;
    }
}

@mixin min-large() {
    @media (min-width:$width-large) {
        @content;
    }
}

@mixin min-xlarge() {
